body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
  width: 100%;
  min-height: 100vh;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.body {
  background: linear-gradient(#8b016a, #6f1159);
  overflow-x: hidden;
}

#header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 30px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10000;
}

.logo {
  font-family: "Rancho", cursive;
  color: #fff;
  font-weight: 700;
  font-style: italic;
  font-size: 3em;
}

.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navigation li {
  margin-left: 20px;
}

.navigation li a {
  padding: 6px 15px;
  color: #fff;
  border-radius: 20px;
  transition: 0.3s all ease-in-out;
}

.navigation li a:hover,
.navigation li a.active {
  background: #fff;
  color: #111;
}

.navigation li a.active:hover {
  color: #a00f7e;
}

section {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

section img {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  pointer-events: none;
}

section img#lamp {
  mix-blend-mode: screen;
}

section img#masjid {
  transform: translateY(-20px);
}

section img#stars2 {
  display: none;
}

#text {
  position: absolute;
  top: 50%;
  right: -100%;
  transform: translateY(-50%);
  color: #fff;
  font-size: 10vw;
  white-space: nowrap;
  text-shadow: 0 10px 15px rgba(0, 0, 0, 0.25);
  font-family: "Rancho", cursive;
}

.sec {
  position: relative;
  padding: 100px;
  background: #96283a;
}

.sec h2 {
  font-size: 4em;
  color: #fff;
  margin-bottom: 10px;
  font-family: "Rancho", cursive;
}

.sec p {
  font-size: 2em;
  color: #fff;
  font-family: "Rancho", cursive;
  width: 310px;
}

@media (max-width: 992px) {
  #header {
    padding: 20px;
  }

  .navigation {
    display: none;
  }

  .toggle {
    position: relative;
    width: 30px;
    height: 30px;
    cursor: pointer;
    background: url(../img/menu.webp);
    background-size: 30px;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 10000;
  }

  .toggle.active {
    position: fixed;
    top: 20px;
    right: 20px;
    background: url(../img/close.webp);
    background-size: 25px;
    background-position: center;
    background-repeat: no-repeat;
  }

  .navigation.active {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: #6f1159;
    flex-direction: column;
  }

  .navigation.active li {
    margin-left: 0;
  }

  .navigation.active li a {
    font-size: 1.5em;
    margin: 5px 0;
    display: inline-block;
    border-radius: 30px;
  }

  section img#stars2 {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  section img#masjid {
    transform: translateY(-5px);
  }

  #text {
    font-size: 15vw;
  }
}
@media (max-width: 590px) {
  .sec {
    padding: 10px;
  }

  .sec h2 {
    font-size: 2.5em;
  }

  .sec p {
    width: 95%;
    font-size: 1.6em;
  }
}